import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Index",
    component: () => import("../views/Index.vue")
  },
  {
    path: "/modeler",
    name: "编辑模式",
    component: () => import("../views/modeler.vue")
  },
  {
    path: "/viewer",
    name: "预览模式",
    component: () => import("../views/viewer.vue")
  }
];

const router = new VueRouter({
  routes
});

export default router;
